#root {
  margin: 0 auto;
  padding: .2rem;
  text-align: center;
}



.logo {
  height: 6em;
  padding: 1.5em;
  will-change: filter;
}
.logo:hover {
  filter: drop-shadow(0 0 2em #646cffaa);
}
.logo.react:hover {
  filter: drop-shadow(0 0 2em #61dafbaa);
}

@keyframes logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@media (prefers-reduced-motion: no-preference) {
  a:nth-of-type(2) .logo {
    animation: logo-spin infinite 20s linear;
  }
}

.card {
  padding: 2em;
}

.read-the-docs {
  color: #888;
}



.group_title {
  color: #666;
  margin: 15px 7px 5px;
  font-weight: 600;
  font-size: 19px;
}

.separator {
  border-top: 1px solid #C8c8C8;
  padding: 5px;
  margin: 15px 5px;
}


.checklabel {
  color: #757575;
  margin: 0 7px;
  font-weight: 600;
}


.opacityView
  {
    --frm-opacity: opacity 550ms ease-in;
    -webkit-transition: var(--frm-opacity);
    -o-transition: var(--frm-opacity);
    -moz-transition: var(--frm-opacity);
    transition: var(--frm-opacity);
  }


  .palette_body {
    --color_text_2: #202124;
    --color_text_02: #000;
    --color_text_18: #181818;
    --color_text_0c: #0c0c0c;
    --color_text_f9: #f9f9f9;
    --color_text_dad: #dadce0;
    --color_text_fff: #fff;
}


.icon{
  cursor: pointer;
}

